.App {
  text-align: center; }

.App-header {
  background-color: #222;
  padding: 20px;
  color: white; }

.App-spinner {
  font-family: monospace; }

[data-root='CharCodeTable'] {
  margin: 1em auto;
  border-spacing: 1em 0; }
  [data-root='CharCodeTable'] th {
    border-bottom: 1px solid rgba(0, 0, 0, 0.1);
    padding: 0.5em 0; }
  [data-root='CharCodeTable'] td {
    font-family: monospace;
    min-width: 100px;
    padding: 0.25em 0;
    font-size: 3em; }
  [data-root='CharCodeTable'] sub {
    font-size: 0.4em;
    opacity: 0.5;
    font-weight: bold; }

[data-root='CharInput'] {
  display: block;
  margin: 1em 0; }
  [data-root='CharInput'] label, [data-root='CharInput'] input {
    font-size: 2em;
    font-weight: bold;
    color: white; }
  [data-root='CharInput'] label {
    opacity: 0.5; }
  [data-root='CharInput'] input {
    background: none;
    border: none;
    border-radius: 0;
    border-bottom: 0.1em solid rgba(255, 255, 255, 0.5);
    width: 1.2em;
    padding: 0;
    margin-left: 0.25em;
    font-family: 'Monaco' monospace;
    text-align: center; }
    [data-root='CharInput'] input:focus {
      outline: none;
      background: rgba(255, 255, 255, 0.1);
      border-bottom: 0.1em solid #fff; }



